.painel.usuarios,
.painel.perfil{
  height: 100%;
}
.painel.usuarios .container,
.painel.perfil .container{
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}
.painel.usuarios .container h2,
.painel.perfil .container h2{
  font-size: 24px;
  text-transform: uppercase;
  line-height: 36px;
  margin-bottom: 40px;
}
.painel.usuarios .container .esquerda{
  width: 100%;
  max-width: 450px;
  padding: 0 30px;
  height: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  flex-direction: column;
}
.painel.perfil .container .esquerda{
  width: 100%;
  padding: 0 30px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

}
.painel.usuarios .container .esquerda form,
.painel.perfil .container .esquerda form{
  width: 100%;
  max-width: 320px;
}
.painel.usuarios .container .esquerda form .grupo-inputs,
.painel.perfil .container .esquerda form .grupo-inputs{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 10px;
  background: #ffff;
  border-radius: 8px;
  border: 1px solid #ccc;
  margin-bottom: 16px;
}
.painel.usuarios .container .esquerda form .grupo-inputs label,
.painel.perfil .container .esquerda form .grupo-inputs label{
  display: flex;
  align-items: center;
  justify-content: center;
}
.painel.usuarios .container .esquerda form .grupo-inputs input,
.painel.perfil .container .esquerda form .grupo-inputs input{
  width: 100%;
  margin-left: 20px;
  padding: 10px;
}
.painel.usuarios .container .esquerda form button,
.painel.perfil .container .esquerda form button{
  background-color: #00CAAA;
  box-shadow: 0 4px 8px rgba(0,202,170,0.25);
  transition: 0.35s ease-in-out;
  -webkit-transition: 0.35s ease-in-out;
  margin-top: 25px;
  width: 100%;
  text-align: center;
  padding: 20px;
  color: #fff;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 32px;
  font-family: 'Ubuntu', sans-serif;
}

.painel.usuarios .container .esquerda form button:hover,
.painel.perfil .container .esquerda form button:hover{
  background-color: #00af93;
}

.painel.usuarios .container .direita{
  width: 100%;
  padding: 0 15px;
  height: 100%;
}
