.card{
  width: 100%;
  height: 210px;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: space-between;
  padding: 24px;
  /* box-shadow: 0px 24px 64px -20px rgba(47, 46, 65, 0.25); */
  border-radius: 16px;
  background-color: #fff;
  margin-bottom: 30px;
  cursor: pointer;

}
.card:hover{
  box-shadow: 0px 21px 21px -20px rgba(47,46,65,0.25)
}
.card .description{}
.card .description h3{
  font-size: 22px;
  line-height: 30px;
  margin-bottom: 8px;
}
.card .description p{
  font-size: 14px;
  color: #666;
  line-height: 21px;
}
.card .progresso{
  width: 100%;
}
.card .progresso h3{
  font-size: 35px;
  margin-bottom: 8px;
}
.card .progresso small{
  font-size: 18px;
}
.card .progress{
  height: 24px;
  border-radius: 12px;
  background-color: #ccc;
  width: 100%;
  height: 12px;
}
.card .progress .bar-progress{
  background-color: teal;
  height: 12px;
  border-radius: 12px;
  text-align: center;
  color: #fff;
  font-size: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}


.slick-slide > div{
  margin: 0 3px;
}