.form-cadastro{
  width: 100%;
}
.form-cadastro .group-inputs{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 10px;
  background: #ffff;
  border-radius: 8px;
  border: 1px solid #ccc;
  margin-bottom: 16px;
}
