  width: 100%;
  margin-left: 20px;
  padding: 10px;
}
.form-cadastro button{
  background-color: #00CAAA;
  box-shadow: 0 4px 8px rgba(0,202,170,0.25);
  transition: 0.35s ease-in-out;
  -webkit-transition: 0.35s ease-in-out;
  width: 100%;
  text-align: center;
  padding: 20px;
  color: #fff;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 32px;
  font-family: 'Ubuntu', sans-serif;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.form-cadastro button:hover{
  background-color: #00af93;
}

.form-cadastro button .loading{
  position: absolute;
  right: 20px;
  width: 30px!important;

}
