.equipamentos .tituloEquipamento{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 8px;
}
.equipamentos h2{
  text-transform: uppercase;
  font-size: 36px;
  letter-spacing: 1.5px;
  margin-bottom: 20px;
}

.list-equipamentos{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  width: 100%;
}
.list-equipamentos li{
  display: flex;
  align-items: flex-start;
  justify-content: center;
  background-color: #fff;
  flex-direction: column;
  padding: 24px;
  border-radius: 16px;
}

.list-equipamentos li h3{
  font-size: 22px;
}

.card-equipamentos{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.list-equipamentos .progress{
  display: flex;
  align-items: flex-start;
  width: 100%;
  height: 20;
  font-size: 10px;
}
.list-equipamentos .progress .barra{
  height: 8px;
  display: flex;
  align-items: center;
  justify-content:space-between;
  color: #fff;
  padding: 0 10px;
}
.list-equipamentos .progress .green{
  border-top-left-radius: 12px;
  border-bottom-left-radius: 12px;
  background-color: #20E647;
  width: 25%;
}
.list-equipamentos .progress .barra span{}
.list-equipamentos .progress .yellow{
  background-color: #ffd307;
  width: 25%;
}
.list-equipamentos .progress .red{
  background-color: #f44336;
  width: 50%;
  border-top-right-radius: 12px;
  border-bottom-right-radius: 12px;
}

.cima{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 5px;
  font-size: 13px!important;
}
.progress1{
  width: 25%;
}
.progress2{
  width: 25%;
}
.progress3{
  width: 50%;
}
.progress1 .barra,
.progress2 .barra,
.progress3 .barra{
  width: 100%!important;

}

.list-legenda{
width: 100%;
}


.list-legenda ul{
  width: 100%;
}
.list-legenda ul li{
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 0;
  flex-direction: row;
  padding-bottom: 5px;
}
.list-legenda ul li span{
  width: 10px;
  height: 10px;
  border-radius: 50%;
}
.list-legenda ul li span.green{
  background-color: #20E647;
}
.list-legenda ul li span.yellow{
  background-color: #ffd307;
}
.list-legenda ul li span.red{
  background-color: #f44336;
}
.list-legenda ul li label{
  margin-left: 10px;
}

.status-medicoes{
  display: flex;
  align-items: flex-start;
  width: 100%;
  flex-direction: column;
  margin-top: 10px;
}

.status-medicoes .fundoStatus{
  width: 100%;
  height: 8px;
  background: #f8f8f8;
  border-radius: 6px;
  margin-bottom: 12px;
}

.status-medicoes .fundoStatus div{}

.status-medicoes .medicaoHumidade,
.status-medicoes .medicaoTemperatura,
.status-medicoes .medicaoGas{
  height: 8px;
  border-radius: 6px;
  max-width: 100%;
}

.statusHumidade,
.statusTemperatura,
.statusGas{
  display: flex;
    align-items: flex-start;
    width: 100%;
    flex-direction: column;
}

.statusHumidade span,
.statusTemperatura span,
.statusGas span{
  margin-bottom: 4px;
  font-weight: 500;
}

.infoMedicao{
  display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.footer-medicoes{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-top: 12px;
}
.footer-medicoes .atualizado{
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}
.footer-medicoes .atualizado .title{
  font-weight: 500;
  margin-bottom: 4px;
}
.footer-medicoes .atualizado .horario{}
.footer-medicoes button{
  display: flex;
  align-items: center;
  padding: 10px;
  background: #0085ca;
  color: #fff;
  font-size: 11px;
  text-transform: uppercase;
}
.footer-medicoes button:hover{
  opacity: 0.8;
}