.menu{
  width: 66px;
  max-width: 370px;
  transition: width 0.2s;
  -webkit-transition: width 0.2s;
  background-color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  right: auto;
  height: 100vh;
  overflow: hidden;
  white-space: nowrap;
  z-index: 999;
}
.menu:hover{
  width: 370px;
  box-shadow: 0px 0px 16px rgb(198 198 198 / 50%);
}

.menu .menu-user{
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-left: 13px;
  padding-top: 24px;
}
.menu .menu-user img{
  max-width: 40px;
}
.menu .menu-user .user{
  padding-right: 13px;
  margin-left: 13px;
}
.menu .menu-user .user h6{
  font-size: 18px;
  letter-spacing: 1.5px;
  line-height: 27px;
  color: #524E4E;
}
.menu .menu-user .user p{
  font-size: 14px;
  color: #6a6a6a;
  letter-spacing: 1.5px;
  line-height: 21px;
  font-style: italic;
}

.menu .menu-list{
  padding-left: 8px;
  padding-right: 8px;
  margin-top: 24px;
  white-space: nowrap;
}
.menu .menu-list li{
  margin-bottom: 8px;
}
.menu .menu-list li a{
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 13px 0px;
  padding-left: 16px;
}
.menu .menu-list li a svg{
  width: 18px;
  min-width: 18px;
  height: 18px;
}
.menu .menu-list li a span{
  margin-left: 24px;
  font-size: 20px;
  font-family: 'Ubuntu', 'Roboto', sans-serif;
  font-weight: 500;
  color: #524E4E;
  font-style: italic;
}

.menu .selected,
.menu .menu-list li a:hover{
  background-color: #607D8B;
  color: #fff;
  border-radius: 50px;
}
.menu .selected svg,
.menu .menu-list li a:hover svg{
  color: #fff!important;
}
.menu .selected span,
.menu .menu-list li a:hover span{
  color: #fff!important;
}

.menu .selected{
  box-shadow: 4px 12px 10px -12px rgba(0,0,0,0.5);
}

.arrow_box{
  padding: 10px;
  min-width: 60px;
}

.arrow_box.green{}
.arrow_box.yellow{}
.arrow_box.red{}

.arrow_box .situacao{
  margin-top: 5px;
}
.arrow_box.green .situacao{
  color: #66Da26;
}
.arrow_box.yellow .situacao{
  color: #ffd307;
}
.arrow_box.orange .situacao{
  color: #ff8100;
}
.arrow_box.red .situacao{
  color: #E91E63;
}
.arrow_box.blue .situacao{
  color: #0085CA;
}

.arrow_box.green .bg-situacao{
  background-color: #66Da26;
}
.arrow_box.yellow .bg-situacao{
  background-color: #ffd307;
}
.arrow_box.orange .bg-situacao{
  background-color: #ff8100;
}
.arrow_box.red .bg-situacao{
  background-color: #E91E63;
}
.arrow_box.blue .bg-situacao{
  background-color: #0085CA;
}

.arrow_box .tooltip-situacao{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.arrow_box .tooltip-situacao span{}
.arrow_box .tooltip-situacao .bg-situacao{
  width: 10px;
  height: 10px;
  margin-right: 5px;
  border-radius: 50%;
}