.users{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 16px;
  width: 100%;
}
.users li{
  width: 100%;
  background-color: #fff;
  padding: 16px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  transition: 0.25s ease;
  -webkit-transition: 0.25s ease;
  cursor: default;
}

.users li:hover{
  box-shadow: 0px 0px 12px -4px rgba(103,103,103, 0.5);
}


.users li .users-info{
}
.users li h3{
  font-size: 14px;
  line-height: 24px;
  color: #666;
}
.users li p{
  font-size: 12px;
  line-height: 21px;
  color: #7a7a7a
}

.users li .users-actions{
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  flex-direction: column;
}
.users li button{}
.users li button:hover{
    transform: translateY(-1px);
      filter: drop-shadow(3px 3px 1px rgba(103, 103, 103, 0.2));
}