.modal-cadastro-grupo.open{
  display: flex;
}
.modal-cadastro-grupo{
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.8);
  z-index: 99;

  align-items: center;
  justify-content: center;
}
.modal-cadastro-grupo .modal-container{
  background-color: #fff;
  border-radius: 8px;
  padding: 72px;
  max-width: 90%;
  width: 538px;
}
.modal-cadastro-grupo .modal-container h2{
  font-size: 30px;
  color: #524E4E;
}
.modal-cadastro-grupo .modal-container p{
  font-size: 14px;
  margin-top: 8px;
  color: #7D7D7D;
}
.modal-cadastro-grupo .modal-container form{
  margin-top: 24px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
  width: 100%;
}
.modal-cadastro-grupo .modal-container form label{
  margin-bottom: 12px;
  font-size: 14px;
  font-weight: 700;
  color: #7D7D7D;
}
.modal-cadastro-grupo .modal-container form input{
  border-bottom: 1px solid #7D7D7D;
  width: 100%;
  padding: 5px;
  border-radius: 0;
  margin-bottom: 24px;
  font-size: 18px;
  color: #7D7D7D;
}
.modal-cadastro-grupo .modal-container form select{
  border-radius: 0;
  width: 100%;
  padding: 10px;
  border-bottom: 1px solid #7d7d7d;
  color: #7d7d7d;
  font-size: 18px;
  margin-bottom: 24px;
}
.modal-cadastro-grupo .modal-container form .css-2b097c-container{
  width: 100%;
}

.modal-cadastro-grupo .modal-container form .buttons{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin-top: 24px;
}
.modal-cadastro-grupo .modal-container form .buttons .cancelar{
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #524E4E;
  width: 48px;
  min-width: 48px;
  border-radius: 8px;
  height: 48px;
  margin-right: 12px;
}
.modal-cadastro-grupo .modal-container form .buttons .cadastrar{
  width: 100%;
  background-color: #0572A3;
  color: #fff;
  font-weight: 700;
  height: 48px;
  border-radius: 8px;
}
