.login-container{
  width: 100%;
  min-height: 100%;
  display: flex;
  align-items: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-color:rgba(0,66,100,0.08);
}
.login-container .container{
  margin: 0 auto;
  max-width: 86%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 100%;

}
.login-container .container .esquerda{
  width: 70%;
  max-width: 360px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
}
.login-container .container .esquerda img{
  max-width: 100%;
  margin-bottom: 25px;
  width: 320px;
}
.login-container .container .esquerda h1{
  color: #444;
  font-size: 30px;
  line-height: 36px;
  margin-bottom: 15px;
}
.login-container .container .esquerda p{
  color: #888;
  font-size: 16px;
  line-height: 21px;
  margin-bottom: 16px;
}
.login-container .container .esquerda .btnLogin{
  background-color: #00CAAA;
  box-shadow: 0 4px 8px rgba(0,202,170,0.25);
  transition: 0.35s ease-in-out;
  -webkit-transition: 0.35s ease-in-out;
  margin-top: 25px;
  width: 100%;
  text-align: center;
  padding: 20px;
  color: #fff;
  text-transform: uppercase;
  font-weight: 700;
  border-radius: 32px;
  font-family: 'Ubuntu', sans-serif;
}
.login-container .container .esquerda .btnLogin:hover{
  background-color: #00af93;
}
.login-container .container .esquerda .btnCadastro{
  transition: 0.35s ease-in-out;
  -webkit-transition: 0.35s ease-in-out;
  width: 100%;
  text-align: center;
  padding: 20px;
  color: #00caaa;
  text-transform: uppercase;
  font-weight: 700;
  font-family: 'Ubuntu', sans-serif;
}
.login-container .container .esquerda .btnCadastro:hover{
  text-decoration:  underline;
}
.login-container .container .direita{
  max-width: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.login-container .container .direita img{
  width: 90%;
  max-width: 560px;
}

.btnVoltar{
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 16px;
  color: #888;
  font-size: 16px;
  line-height: 21px;
}
.btnVoltar span{
  margin-left: 8px;
}


@media(min-width: 1440px){
  .login-container .container{
    max-width: 1250px;
  }
}