.modal-medicoes{
  display: none;
  align-items: center;
  justify-content: center;
  max-width: 100%;
  padding: 60px;
  margin: 0 auto;
  position: fixed;
  z-index: 99;
  top: 0;
  right: 0;
  height: 100%;
  width: calc(100% - 66px);
  background-color: rgba(0,0,0,0.5);
}
.modal-medicoes.open{
  display: flex;
}

.modal-medicoes .linear{
  width: 100%;
  background-color: #fff;
  box-shadow: 0px 4px 12px -20px #000;
  padding: 60px;
  /* border-radius: 16px; */
  overflow-y: scroll;
  max-height:calc(100vh - 100px);
  position: relative;
}

.modal-medicoes .linear h3{
  margin-bottom: 20px;
  margin-top: 50px;
}

.modal-medicoes h2{
  font-size: 35px;
}