
.painel-grupos{
  display: flex;
  width: 100%;
  min-height: 100%;
  align-items: flex-start;
}
.page-grupos .container{
  flex-direction: column;
}
.page-grupos h2{
  font-size: 30px;
  text-transform: uppercase;
  margin-bottom: 32px;
}

.painel-grupos .lista-grupos{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 24px;
  width: 100%;
}
.painel-grupos .lista-grupos li{
  display: flex;
  align-items: center;
  width: 100%;
  background-color: #fff;
  border-radius: 8px;
  padding: 24px;
  justify-content: space-between;
  transition: 0.25s ease all;
  -webkit-transition: 0.25s ease all;
}

.painel-grupos .lista-grupos li:hover{
  box-shadow: 0px 0px 12px -4px rgba(103,103,103, 0.5);
}

.painel-grupos .lista-grupos li .sigla{
  width: 60px;
  min-width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0,133,202,0.25);
  border-radius: 50%;
  color: #0085CA;
  font-size: 14px;
  line-height: 14px;
  letter-spacing: 1.5px;
  font-weight: 700;
  text-transform: uppercase;
}

.painel-grupos .lista-grupos li .list-grupo-info{
  margin-left: 16px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
}
.painel-grupos .lista-grupos li .list-grupo-info h3{
  text-transform: uppercase;
  font-size: 15px;
  color: #524E4E;
  
}
.painel-grupos .lista-grupos li .list-grupo-info p{
  margin-top: 8px;
  font-style: italic;
  color: #33a02c;
}

.painel-grupos .lista-grupos li .buttons{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  min-height: 76px;
  margin-left: 8px;
}

.painel-grupos .lista-grupos li .buttons button{
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
  border: 2px solid;
  border-radius: 4px;
  max-width: 31px;
}
.painel-grupos .lista-grupos li .buttons button:hover{
  transform: translateY(-1px);
    filter: drop-shadow(3px 3px 1px rgba(103, 103, 103, 0.2));
}

.painel-grupos .lista-grupos li .buttons .btn-lista-grupos-editar{
  border-color: #0572A3;
}
.painel-grupos .lista-grupos li .buttons .btn-lista-grupos-excluir{
  border-color: #FF585A;
}

.painel-grupos .lista-grupos li:last-child{
  cursor: pointer;

}

.painel-grupos .lista-grupos li .novoGrupo{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}
.painel-grupos .lista-grupos li .novoGrupo .icone-novoGrupo{
  background-color: rgba(0,133,202,0.25);
  padding: 16px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;

}
.painel-grupos .lista-grupos li .novoGrupo h3{
  margin-top: 8px;
}
.painel-grupos .lista-grupos li .novoGrupo .icone-novoGrupo svg{
  transition: 0.25s ease all;
  -webkit-transition: 0.25s ease all;
}
.painel-grupos .lista-grupos li:last-child:hover .icone-novoGrupo svg{
  transform: translateY(-1px);
  filter: drop-shadow(3px 3px 1px rgba(0, 0, 0, 0.2));
}